<template>
  <div id="Lysi-table">
    <div id="app-picker">
      <v-row>
        <div class="textOfDate">
        <v-text-field
        v-model="dateRangeText"
        v-on:click="click_text()"
        label="Date range"
        prepend-icon="mdi-calendar"
        readonly
      ></v-text-field>
      <div class="picker-dialog" v-if="state_text_click === true">
        <v-date-picker
          v-model="dates"
          color="rgb(41 149 250)"
          range
          no-title
        ></v-date-picker>
        <v-btn
          v-on:click="click_btn()"
          min-width=89%
          color="rgb(41 149 250)"
          elevation="2"
          >OK</v-btn
        >
      </div>

      </div>
      <div class="exprot">
        <v-btn
        v-on:click="onExport()"
        elevation="2"
        color="rgb(41 149 250)"
        >Export</v-btn
      ></div>
      </v-row>
      
      
      
      
    </div>
    <!-- <div class="btn-export"><v-btn v-on:click="onExport()" elevation="2" color="rgba(60, 179, 113,0.5)">Export</v-btn></div> -->
    <div class="Table-lysimeter">
       <table id="myTable">
      <thead>
        <tr>
          <th>Date</th>
          <th>AWD_Z1</th>
          <th>AWD_Z2</th>
          <th>AWD_Z3</th>
          <th>AWD_Z4</th>
          <th>AWD_Z5</th>
          <th>AWD_Z6</th>
          <th>AWD_Z7</th>
          <th>AWD_Z8</th>
          <th>AWD_Z9</th>
          <th>AWD_Z10</th>
          <th>AWD_Z11</th>
          <th>AWD_Z12</th>
          <th>AWD_Z13</th>
          <th>AWD_Z14</th>
          <th>AWD_Z15</th>
          <th>AWD_Z16</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item of this.sprit_data" v-bind:key="item.id">
          <td>{{ item["Date"].substr(0, 10) }}</td>
          <td>{{ item["AWD_Z1"] }}</td>
          <td>{{ item["AWD_Z2"] }}</td>
          <td>{{ item["AWD_Z3"] }}</td>
          <td>{{ item["AWD_Z4"] }}</td>
          <td>{{ item["AWD_Z5"] }}</td>
          <td>{{ item["AWD_Z6"] }}</td>
          <td>{{ item["AWD_Z7"] }}</td>
          <td>{{ item["AWD_Z8"] }}</td>
          <td>{{ item["AWD_Z9"] }}</td>
          <td>{{ item["AWD_Z10"] }}</td>
          <td>{{ item["AWD_Z11"] }}</td>
          <td>{{ item["AWD_Z12"] }}</td>
          <td>{{ item["AWD_Z13"] }}</td>
          <td>{{ item["AWD_Z14"] }}</td>
          <td>{{ item["AWD_Z15"] }}</td>
          <td>{{ item["AWD_Z16"] }}</td>
        </tr>
      </tbody>
    </table>
    </div>
   

    <!-- <div id="app-picker">
      <v-app id="inspire">
        <v-row>
          <v-col
            cols="12"
            sm="3"
          >
            <v-text-field
              v-model="dateRangeText"
              v-on:click = "click_text()"
              label="Date range"
              prepend-icon="mdi-calendar"
              readonly
            ></v-text-field>
            <div class="picker-dialog"
            v-if="state_text_click === true">
              <v-date-picker
                v-model="dates"
                range
                no-title
              ></v-date-picker>
              <v-btn
                v-on:click="click_btn()"
                block
                color="primary"
                elevation="2"
              >OK</v-btn>
            </div>
            
          </v-col>
        </v-row>
      </v-app>
    </div> -->
  </div>
</template>
<script>
import axios from "axios";
import XLSX from "xlsx"; // import xlsx
export default {
  name: "Lysi-table",
  data() {
    /// get Datenow - 15 day ///
    var d = new Date();
    // console.log(d.toLocaleString())
    // console.log(d.toISOString())
    this.date_now = d.toISOString().substr(0, 10);
    d.setDate(d.getDate() - 15);
    this.date_old = d.toISOString().substr(0, 10);
    /// END get Datenow - 15 day ///
    return {
      info: null,
      sprit_data: [],
      state_text_click: false,
      date_now: null,
      date_old: null,
      dates: [this.date_old, this.date_now],
    };
  },
  computed: {
    dateRangeText() {
      return this.dates.join(" ~ ");
    },
  },
  methods: {
    click_text: function (event) {
      if (this.state_text_click == false) {
        // console.log("click_text_state=true")
        return (this.state_text_click = true);
      } else {
        // console.log("click_text_state=false")
        return (this.state_text_click = false);
      }
    },
    click_btn: function (event) {
      // console.log("click_btn_OK")
      // console.log(this.dates)
      var select_date = this.dates;
      var start_date = new Date(select_date[0]);
      var end_date = new Date(select_date[1]);
      var sprit_date;
      // console.log(select_date[0],select_date[1])

      if (select_date[0] == null || select_date[1] == null) {
        // console.log("no")
        alert("Please Enter Date for Start to END");
      }
      if (start_date > end_date) {
        alert("Please enter a valid start to end date.");
      } else {
        var data_select = [];
        for (var item of this.info) {
          sprit_date = new Date(item.Date.substr(0, 10));
          if (start_date <= sprit_date && sprit_date <= end_date) {
            // console.log(item)
            data_select.push(item);
          }
        }
        // console.log(data_select)
        return (this.state_text_click = false), (this.sprit_data = data_select);
      }

      var data_select = [];
      for (var item of this.info) {
        sprit_date = new Date(item.Date.substr(0, 10));
        if (start_date <= sprit_date && sprit_date <= end_date) {
          console.log(item);
          data_select.push(item);
        }
      }
      console.log(data_select);
      return (this.state_text_click = false), (this.sprit_data = data_select);
    },
    onExport: function (event) {
      const dataWS = XLSX.utils.json_to_sheet(this.info);
      const wb = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, dataWS);
      XLSX.writeFile(wb, "export.xlsx");
    },
  },
  mounted() {
    axios
      .get(
        // "https://script.google.com/macros/s/AKfycbyn9rKaQXdHzu3n-ocdq2OdKJmmsxNOlNRKFhiCSDpxPbxsYfD49mvwd52k1Za92WezIw/exec?action=getUsers"
        // "https://script.google.com/macros/s/AKfycbxxE23SBHICIiZaASF6iRKcHh5aunewTXz5kL0RGZJLa-sOAABHNBCwy-GlehVh8wQ/exec?action=getData"
      "https://script.google.com/macros/s/AKfycbyQvjhX67GAjurkBrO__i5qPuFCqZfC0_h4MLinQbmU82L7fWJE1M8GlcPfn938L08sxw/exec?action=getData"
      )
      .then((response) => {
        this.info = response.data;
        // console.log(response.data);
        var select_date = this.dates;
        var start_date = new Date(select_date[0]);
        var end_date = new Date(select_date[1]);
        var sprit_date;
        var data_filter = [];
        var data_select = [];
        var d;
        ///// filer set Date to correct /////
        for (var item of this.info) {
          d = new Date(item.Date);
          d.setDate(d.getDate(item.Date) + 1);
          item.Date = d.toISOString().substr(0, 10);
          data_filter.push(item);
        }
        // console.log("data_filter")
        // console.log(data_filter)
        ///// filer data to table follow date your select /////
        for (var item of data_filter) {
          sprit_date = new Date(item.Date.substr(0, 10));
          if (start_date <= sprit_date && sprit_date <= end_date) {
            // console.log(item)
            data_select.push(item);
          }
          this.sprit_data = data_select;
          // console.log(data_select)
        }

        $(document).ready(function () {
          $("#myTable").DataTable();
        });
      });
  },
};
</script>
<style scoped>
table {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

td,th {
  border: 1px solid #ddd;
  padding: 8px;
}

table tr:nth-child(even) {
  background-color: #f2f2f2;
}

table tr:hover {
  background-color: #ddd;
}

table th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #2995fa;
  color: white;
}
#app-picker {
  /* position: absolute; */
  width: 290px;
  height: auto;
}
.dataTables_wrapper {
  z-index: 0;
}
.picker-dialog {
  position: absolute;
  z-index: 1;
}
.row {
    display: flex;
    flex: 1 1 auto;
    margin-left: 25px;
    margin-top: 10px;
    flex-wrap: wrap;
}
.Table-lysimeter{
  margin-top:50px;
  width: 70vw;
  text-align: center;
  }
td[data-v-2c40576b], th[data-v-2c40576b] {
    border: 1px solid #ddd;
    padding: 10px 0px;
    font-size: 12px;
    text-align: center;
}
</style>